import React , {Component} from 'react';
import './App.css';
import Home from './Home';
import Allopathic from './Allopathic';
import Homeopathic from './Homeopathic';
import Avedic from './Avedic';
import About from './About';
import Wellness from './Wellness';
import Login from './Login';
import Admin from './Admin';
import Contact from './Contact';
import Signup from './Signup';
 import {BrowserRouter,Route,Switch} from 'react-router-dom';
class App extends Component{
  render()
  {
    return (
       <BrowserRouter>
      <div className="App">
        <Switch>
           <Route path='/' exact component={Home}/>
           <Route path='/Allopathic' component={Allopathic}/>
           <Route path='/Homeopathic'  component={Homeopathic}/>
           <Route path='/Avedic' component={Avedic}/>
           <Route path='/About'  component={About}/>
           <Route path='/Wellness' component={Wellness}/>
           <Route path='/Login' component={Login}></Route>
           <Route path='/Admin' component={Admin}></Route>
           <Route path='/Contact' component={Contact}></Route>
           <Route path='/Signup' component={Signup}></Route>
        </Switch>
        
      </div>
      </BrowserRouter>
        );
  }
}
export default App;
