import React , {Component} from 'react';
import Menu1 from './Menu1';
import Bottom from './Bottom.js';
import {Link} from 'react-router-dom';
import './Signup.css';
import axios from 'axios';

export default class Signup extends Component
{
  constructor(props){
    super(props);
  
    this.state={
      username:" ",
      lastname:" ",
      country:" ",
      degree1:" ",
      degree2:" ",
      degree3:" ",
      approve:" "
              }
        }
     
    onChangeUsername(e)
    {
      this.setstate({
         username:e.target.value
      });
    }
    onChangeLastname(e)
    {
      this.setState({
        lastname:e.target.value
      });
    }
    onChangeCountry(e)
    {
      this.setState({
        country:e.target.value
      });
    }
    onChangeDegree1(e)
    {
      this.setState({
        degree1:e.target.value
      });
    }
    onChangeDegree2(e)
    {
      this.setState({
        degree2:e.target.value
      });
    }
    onChangeDegree3(e)
    {
      this.setState({
        ddegree3:e.target.value
      });
    }
    onSubmit(e)
    {
      e.preventDefault();
      const user={
        username:this.state.username,
        lastname:this.state.lastname,
        country:this.state.country,
        degree1:this.state.degree1,
        degree2:this.state.degree2,
        degree3:this.state.degree3
      }
      axios.post('http://localhost:5000/hey/')
      .then(response=>console.log(response.data));
      window.location="/";
    }
  render(){
  return (<div>  
      <Menu1/>
       <br/>
       <br/>
       <br/>
       <br/>
       <br/>
       <br/>
      <form onSubmit={this.onSubmit} >
      <fieldset>
    <legend>SIGN UP:</legend>
    <label for="fname">FIRST NAME:<span className="key">*</span></label>
    <input type="text" id="fname" name="firstname" placeholder="Your name.." value={this.state.username} onChange={this.onChangeUsername}/>
    <label for="lname">LAST NAME:</label>
    <input type="text" id="lname" name="lastname" placeholder="Your last name.." value={this.state.lastname} onChange={this.onChangeLastname}/>
    <br/>
    <label for="country">COUNTRY<span className="key">*</span></label>
    <input type="text" id="lname" name="country" placeholder="Your country.." value={this.state.country} onChange={this.onChangeCountry}/>
    <br/>
    <label for="country">BACHELOR'S DEGREE:<span className="key">*</span></label>
    <input type="text" id="lname" name="country" placeholder="Name of the Degree.." value={this.state.degree1} onChange={this.onChangeDegree1}/>
    <br/>
    <label for="country">POST GRADUATE DEGREE:<span className="key">*</span></label>
    <input type="text" id="lname" name="country" placeholder="Name of the Degree.."  value={this.state.degree2} onChange={this.onChangeDegree2}/>
    <br/>
    <label for="country">SUPER SPECIALITY DEGREE:</label>
    <input type="text" id="lname" name="country" placeholder="Name of the Degree..."  value={this.state.degree3} onChange={this.onChangeDegree3}/>
    <input type="submit" value="Submit"/>
    </fieldset>
      </form>
       Back to <Link to="Login">Log In</Link> page..
      <br/>
      <br/>
      <br/>
      <br/>
      <Bottom/>
    </div>
         )
  }
}